#Skills{
    margin-top: 1.6rem;
}

.skills__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.skills__container > div{
    background: var(--color-bg-variant);
    padding: 2.5rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.skills__container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skills__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
}

.skills__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.skills__details{
    display: flex;
    gap: 1rem;
}


/*Medium Devices media queries*/
@media screen and (max-width: 1800px){
    #Skills{
        margin-top: 32rem;
    }
    
    .skills__container{
        grid-template-columns: 1fr;
    }

    .skills__container > div{
        width:80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills__content{
        padding: 1rem;
    }
  }

  @media screen and (max-width: 1250px){
    #Skills{
        margin-top: 25rem;
    }
  }

  @media screen and (min-width: 900px) and (max-width: 1249px){
    #Skills{
        margin-top: 35rem;
  }
}
  
  
  /*Small Devices media queries*/
  @media screen and (max-width: 600px){
    #Skills{
        margin-top: 78rem;
    }
    
    .skills__container{
        gap: 1rem;
        grid-template-columns: 1fr;
    }

    .skills__container > div{
        width:100%;
        padding: 2rem 1rem;
    }
  }

  @media screen and (max-width: 400px){
    #Skills{
        margin-top: 88rem;
    } 
  }
